import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import {PlaceModel} from "../../models/placemodel";
import {PlaceService} from "../../service/placeservice";
import {PlaceFormPage} from "../place-form/place-form";
import {MyPlaceInfoModel} from "../../models/MyPlaceInfoModel";
import {MyPlaceFormPage} from "./form/myplaceform";
import {PlaceDetailPage} from "../place-detail/place-detail";

@Component({
  templateUrl: 'myplacelist.html'
})
export class MyPlaceListPage {

  places: PlaceMap = {};
  myplaces: MyPlaceInfoModel[] = [];

  constructor(public navCtrl: NavController, private placeService: PlaceService) {
      this.refreshList();
  }

  ionViewWillEnter() {
    this.refreshList();
  }

  refreshList() {
    this.placeService.getPlaces().then((placeList) => {
      if (placeList) {
        placeList.forEach((pl) => {
          this.places[pl.num] = pl;
        });

        this.placeService.getMyPlaces().then((mylist) => {
          console.log("my place list", mylist);
          this.myplaces = mylist;
        });
      }
    });
  }

  create() {
    this.navCtrl.push(MyPlaceFormPage, {
      id: 0
    });
  }

  remove(id) {
    this.placeService.removeFromMyPlaceList(id).then(() => {
      this.refreshList();
    })
  }

  open(id) {
    this.navCtrl.push(PlaceDetailPage, {
      id: id
    });
  }
}

export interface PlaceMap {
  [place: number] : PlaceModel;
}



