import {Component, Input} from '@angular/core';
import {NavController} from 'ionic-angular';
import {FlatType, PlaceFlatModel} from "../../../models/placeflat";
import {PlaceFlatDetailPage} from "../../place-flat-detail/placeflat-detail";
import {PlaceModel} from "../../../models/placemodel";


@Component({
  selector: 'place-flat',
  templateUrl: 'placeflat.html'
})
export class PlaceFlatComponent {
  @Input("flat") flat: PlaceFlatModel;
  @Input("place") place: PlaceModel;

  flatType = FlatType;

  constructor(public navCtrl: NavController) {
    // console.log("flat: " + this.type);
  }

  public showDetail() {
    console.log("Show details", this.place.num, this.flat.num);
    this.navCtrl.push(PlaceFlatDetailPage, {
      placeid: this.place.num,
      id: this.flat.num
    });
  }

}



