import {Component} from '@angular/core';
import {NavController, NavParams} from 'ionic-angular';
import {PlaceService} from "../../service/placeservice";
import {FlatType} from "../../models/placeflat";
import {PlaceModel} from "../../models/placemodel";
import {PlaceFloorModel} from "../../models/placefloor";

@Component({
  selector: 'place-detail-page',
  templateUrl: 'place-detail.html'
})
export class PlaceDetailPage {

  public place: PlaceModel;
  public floorList: PlaceFloorModel[];
  public dangerList: number[] = [];

  id: number;

  constructor(public navCtrl: NavController, private navParams: NavParams,
              private placeService: PlaceService) {
    this.id = navParams.get("id");
    this.refreshPlan();
  }

  ionViewWillEnter() {
    this.refreshPlan();
  }

  refreshPlan() {
    Promise.all([this.placeService.getPlace(this.id), this.placeService.getMyPlaceDetail(this.id)])
      .then((results) => {
        const pl = results[0];
        const myhistory = results[1];
        console.log("pl", pl, "his", myhistory);

        this.place = pl;
        if (myhistory) {
          pl.floorPlan.forEach(floor => {
            floor.flatList.forEach(flat => {
              let fhis = myhistory.flatHistoryMap[flat.num];
              if (fhis) {
                if (flat.type != FlatType.DANGER && flat.type != FlatType.SKIP
                  && fhis.type != FlatType.DEFAULT) {
                  flat.type = fhis.type;
                }
                flat.noRing = fhis.noRing;
                flat.noDmf = fhis.noDmf;
              }
            })
          });
        }
        this.floorList = pl.floorPlan;
        this.dangerList = this.placeService.getDangerList(this.floorList);
      });
  }

}



