import { Component } from '@angular/core';
import {NavController, NavParams} from 'ionic-angular';
import {PlaceMap} from "../myplacelist";
import {PlaceService} from "../../../service/placeservice";
import {PlaceModel} from "../../../models/placemodel";
import {MyPlaceInfoModel} from "../../../models/MyPlaceInfoModel";

@Component({
  templateUrl: 'myplaceform.html'
})
export class MyPlaceFormPage {

  places: PlaceMap = {};
  placeList: PlaceModel[] = [];

  info: number;
  isSaving: boolean = false;

  constructor(private navParams: NavParams, public navCtrl: NavController,
              private placeService: PlaceService) {
    this.info = navParams.get("id");
    this.placeService.getPlaces().then((placeList) => {
      if (placeList) {
        this.placeList = placeList;
        placeList.forEach((pl) => {
          this.places[pl.num] = pl;
        })
      }
    });
  }

  save() {
    console.log("save", this.info);
    if (this.info && this.info > 0) {
      this.isSaving = true;
      let myplace = new MyPlaceInfoModel(this.info);
      this.placeService.addToMyPlaceList(myplace).then(() => {
        this.isSaving = false;
        this.navCtrl.pop();
      });
    }
  }
}



