import {Component, Input} from '@angular/core';
import {NavController, NavParams} from 'ionic-angular';
import {FlatType, PlaceFlatModel} from "../../models/placeflat";
import {PlaceModel} from "../../models/placemodel";
import {FlatHistory, MyPlaceModel} from "../../models/MyPlaceModel";
import {PlaceService} from "../../service/placeservice";

@Component({
  selector: 'place-flat-detail',
  templateUrl: 'placeflat-detail.html'
})
export class PlaceFlatDetailPage {
  // @Input("flat") flat: PlaceFlatModel; // 0 - default, 1 - good, 2 - visited, 3 - danger, 4 - new danger

  flatid: number;
  placeid: number;

  place: PlaceModel;
  myplace: MyPlaceModel;
  history: FlatHistory = new FlatHistory();

  flatType = FlatType;

  constructor(public navCtrl: NavController, private navParams: NavParams,
              private placeService: PlaceService) {
    this.flatid = navParams.get("id");
    this.placeid = navParams.get("placeid");
    console.log("Show details for", this.flatid, this.placeid);
    Promise.all([this.placeService.getPlace(this.placeid), this.placeService.getMyPlaceDetail(this.placeid)])
      .then((results) => {
        console.log("results", results);
        const pl = results[0];
        const myhistory = results[1];

        this.place = pl;
        this.myplace = myhistory || new MyPlaceModel(this.placeid);

        if (myhistory) {
          this.history = myhistory.flatHistoryMap[this.flatid] || new FlatHistory();
        }

      })
  }

  setType(type: FlatType) {
    this.history.type = type;
    this.history.lastVisit = new Date().getTime();
    this.saveHistory().then(() => {
      this.navCtrl.pop();
    });
  }

  saveHistory() {
    this.myplace.flatHistoryMap[this.flatid] = this.history;
    MyPlaceModel.refreshCounters(this.myplace);
    this.placeService.saveMyPlaceDetail(this.placeid, this.myplace);
    console.log("Save history", this.history);
    return this.placeService.getMyPlaceInfo(this.placeid).then((info) => {
      info.visited = this.myplace.visited;
      info.unvisited = this.myplace.unvisited;
      this.placeService.setMyPlaceInfo(info);
    });
  }

  setNoRing(flag) {
    this.history.noRing = flag;
    this.saveHistory();
    this.saveHistory().then(() => {
      this.navCtrl.pop();
    });
  }

  setNoDmf(flag) {
    this.history.noDmf = flag;
    this.saveHistory().then(() => {
      this.navCtrl.pop();
    });
    this.navCtrl.pop();
  }

}



