import { NgModule, ErrorHandler } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { IonicApp, IonicModule, IonicErrorHandler } from 'ionic-angular';
import { MyApp } from './app.component';
import { MomentModule } from 'angular2-moment';

import { IonicStorageModule } from '@ionic/storage';


import { AboutPage } from '../pages/about/about';
import { ContactPage } from '../pages/contact/contact';
import { HomePage } from '../pages/home/home';
import { TabsPage } from '../pages/tabs/tabs';

//PAGES
import { PlacePage } from '../pages/place/place';
import { PlaceFormPage } from '../pages/place-form/place-form';
import { PlaceDetailPage } from '../pages/place-detail/place-detail';
import { PlaceFlatDetailPage } from '../pages/place-flat-detail/placeflat-detail';
import { MyPlaceListPage } from '../pages/myplace/myplacelist';
import { MyPlaceFormPage } from '../pages/myplace/form/myplaceform';

//SERVICES
import { StorageService } from '../service/storageservice';
import { PlaceService } from '../service/placeservice';

//COMPONENTS
import { PlaceFlatComponent } from '../pages/place-detail/flat/placeflat';
import { PlaceFlatBlockComponent } from '../pages/place-form/flat-block/placeflat-block';


import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';

@NgModule({
  declarations: [
    MyApp,
    AboutPage,
    ContactPage,
    HomePage,
    TabsPage,

    PlacePage,
    PlaceFormPage,
    PlaceDetailPage,
    PlaceFlatDetailPage,
    MyPlaceListPage,
    MyPlaceFormPage,


    PlaceFlatComponent,
    PlaceFlatBlockComponent
  ],
  imports: [
    BrowserModule,
    IonicModule.forRoot(MyApp),
    IonicStorageModule.forRoot({
      name: '__myjw',
      // driverOrder: ['indexeddb', 'sqlite', 'websql']
    }),
    MomentModule
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    AboutPage,
    ContactPage,
    HomePage,
    TabsPage,

    PlacePage,
    PlaceFormPage,
    PlaceDetailPage,
    PlaceFlatDetailPage,
    MyPlaceListPage,
    MyPlaceFormPage

  ],
  providers: [
    StatusBar,
    SplashScreen,
    {provide: ErrorHandler, useClass: IonicErrorHandler},

    StorageService, PlaceService
  ]
})
export class AppModule {}



