import {FlatType} from "./placeflat";

export class MyPlaceModel {
  id: number;
  flatHistoryMap: FlatHistoryMap = {};

  unvisited: number = 0;
  visited: number = 0;

  constructor(id: number) {
    this.id = id;
  }

  static refreshCounters(model: MyPlaceModel) {
    let  visited = 0, unvisited = 0;
    for (let flnum in model.flatHistoryMap) {
      if (model.flatHistoryMap[flnum] &&
        (model.flatHistoryMap[flnum].type == FlatType.VISITED
          || model.flatHistoryMap[flnum].type == FlatType.NEWDANGER
          || model.flatHistoryMap[flnum].type == FlatType.GOOD)) {
        visited++
      }
    }
    model.visited = visited;
  }
}

export interface FlatHistoryMap {
  [flatMap: number] : FlatHistory;
}

export class FlatHistory {
  flatNum: number;
  type: FlatType = FlatType.DEFAULT;
  people: string = "";

  noRing: boolean = false;
  noDmf: boolean = false;

  comments: PlaceComment[] = [];
  lastVisit: number = 0;
}

export class PlaceComment {
  text: string = "";
  time: number = new Date().getTime();
}



