import {PlaceFloorModel} from "./placefloor";

export class PlaceModel {

  public num: number;
  public title: string;
  public totalFlats: number = 0;
  public floors: number = 0;
  public dangerFloors: number = 0;

  public floorPlan: PlaceFloorModel[] = [];

  constructor(num: number, title: string) {
    this.num = num;
    this.title = title;
  }

}



