import {Injectable} from "@angular/core";
import {StorageService} from "./storageservice";
import {FlatType, PlaceFlatModel} from "../models/placeflat";
import {PlaceFloorModel} from "../models/placefloor";
import {PlaceModel} from "../models/placemodel";
import {PlaceFormInput} from "../pages/place-form/place-form";
import {MyPlaceInfoModel} from "../models/MyPlaceInfoModel";
import {MyPlaceModel} from "../models/MyPlaceModel";

@Injectable()
export class PlaceService {

  readonly MYPLACELISTKEY: string = "MYPLACELISTKEY";
  readonly PLACEKEY: string = "PLACES";
  readonly PLACE_ID_KEY_PREFIX: string = "PLACE_";

  constructor(private storageService: StorageService) {
    console.log("INIT PlaceService");
  }


  getPlaces(): Promise<PlaceModel[]> {
    return this.storageService.get(this.PLACEKEY)
      .then(places => {
        console.log("Load my place list", places);
        if (!places) {
          return [];
        }
        return places;
      });
  }

  getPlace(idplace): Promise<PlaceModel> {
    return this.getPlaces().then((placeList: PlaceModel[]) => {
      let result:PlaceModel = null;
      if (placeList) {
        placeList.forEach((pm, i) => {
            if (pm.num == idplace) {
              console.log("FOUND", pm);
              result = pm;
            }
          });
      }
      return result;
    })
  }

  savePlace(toSavePlace: PlaceModel) {
    console.log("save place", toSavePlace);
    return this.getPlaces().then(placeList => {
      let toSaveN = -1, lastN = 0;
      placeList.forEach((place, n)=> {
        if (place.num == toSavePlace.num) {
          console.log("Already exist place", n);
          toSaveN = n;
        }
        if (place.num < toSavePlace.num) {
          lastN = n;
        }
      });
      if (toSaveN >= 0) {
        placeList[toSaveN]= toSavePlace;
      } else {
        placeList.splice(lastN, 0, toSavePlace);
      }
      console.log("to save placelist", placeList);
      this.savePlaceList(placeList);
      return placeList;
    })
  }

  savePlaceList(placeList) {
    placeList.sort((a,b) => {
      return a.num - b.num;
    });
    return this.storageService.set(this.PLACEKEY, placeList);
  }

  remove(id) {
    return this.getPlaces().then(placeList => {
      return this.savePlaceList(placeList.filter((p => p.num != id)))
    })
  }

  addPlace(input: PlaceFormInput, floorPlan: PlaceFloorModel[]) {
    console.log("addPlace", input, floorPlan);
    if (input.num > 0) {
      return this.getPlace(input.num).then(place => {
        if (!place) {
          place = new PlaceModel(input.num, input.address);
        }
        place.floorPlan = floorPlan;
        place.floors = floorPlan.length;
        let flatCount = 0, dangerCount = 0;
        floorPlan.forEach(floor => {
          floor.flatList.forEach((flat:PlaceFlatModel) => {
            if (flat.type != FlatType.SKIP) {
              flatCount++;
            }
            if (flat.type.valueOf() == FlatType.DANGER.valueOf()) {
              dangerCount++;
            }

          })
        });
        place.title = input.address;
        place.totalFlats = flatCount;
        place.dangerFloors = dangerCount;
        console.log("save place", place);
        return this.savePlace(place);
      })
    }
    return Promise.resolve([]);
  }

  getMyPlaces(): Promise<MyPlaceInfoModel[]> {
    return this.storageService.get(this.MYPLACELISTKEY).then(myplaces => {
      return myplaces || [];
    });
  }

  getMyPlaceInfo(id): Promise<MyPlaceInfoModel> {
    return this.getMyPlaces().then(myplaces => {
      return myplaces.filter(mypl => mypl.id == id).shift();
    });
  }

  setMyPlaceInfo(myplace: MyPlaceInfoModel) {
    return this.getMyPlaces().then((myplaces) => {
      let n = -1;
      myplaces.forEach((pl, i) => {
        if (pl.id == myplace.id) {
          n = i;
        }
      });
      if (n >= 0) {
        myplaces[n] = myplace;
      }
      return this.storageService.set(this.MYPLACELISTKEY, myplaces);
    })
  }

  getMyPlaceDetail(id): Promise<MyPlaceModel> {
    return this.storageService.get(this.PLACE_ID_KEY_PREFIX + id);
  }

  saveMyPlaceDetail(id, myplace): Promise<MyPlaceModel> {
    return this.storageService.set(this.PLACE_ID_KEY_PREFIX + id, myplace);
  }

  addToMyPlaceList(info: MyPlaceInfoModel) {
    console.log("Add to my place list", info);
    return this.getMyPlaces().then((mylist) => {
      info.addTime = new Date().getTime();
      if (!mylist) {
        mylist = [];
      }
      var exist = mylist.filter(value => value.id == info.id).shift();
      if (!exist) {
        mylist.push(info);
      }
      return this.storageService.set(this.MYPLACELISTKEY, mylist);
    })
  }

  removeFromMyPlaceList(id: number) {
    return this.getMyPlaces().then((mylist) => {
      mylist = mylist.filter(value => value.id != id);
      this.storageService.set(this.MYPLACELISTKEY, mylist);
      this.storageService.remove(this.PLACE_ID_KEY_PREFIX + id);
      return mylist;
    })
  }

  getDangerList(floorPlan) {
    let list: number[] = [];
    if (floorPlan) {
      floorPlan.forEach((floor) => {
        if (floor.flatList) {
          floor.flatList.forEach((flat) => {
            if (flat.type == FlatType.DANGER) {
              list.push(flat.num);
            }
          })
        }
      })
    }
    return list;
  }

  getFlatList(idplace): Promise<PlaceFloorModel[]> {
    return Promise.resolve([
      new PlaceFloorModel(1,
      [new PlaceFlatModel(idplace, 1, 0, 1),
              new PlaceFlatModel(idplace, 2, 0, 1),
              new PlaceFlatModel(idplace, 3, 0, 1),
              new PlaceFlatModel(idplace, 4, 0, 1)]
      ),
      new PlaceFloorModel(2,
        [new PlaceFlatModel(idplace, 5, 0, 2),
                new PlaceFlatModel(idplace, 6, 0, 2),
                new PlaceFlatModel(idplace, 7, 0, 2),
                new PlaceFlatModel(idplace, 8, 0, 2)]
      ),

      new PlaceFloorModel(3,
      [new PlaceFlatModel(idplace, 9, 0, 3),
              new PlaceFlatModel(idplace, 10, 0, 3),
              new PlaceFlatModel(idplace, 11, 0, 3),
              new PlaceFlatModel(idplace, 12, 0, 3)]
      ),

      new PlaceFloorModel(4,
      [new PlaceFlatModel(idplace, 13, 0, 4),
              new PlaceFlatModel(idplace, 14, 0, 4),
              new PlaceFlatModel(idplace, 15, 0, 4),
              new PlaceFlatModel(idplace, 16, 0, 4)]
      ),

      new PlaceFloorModel(5,
      [new PlaceFlatModel(idplace, 17, 0, 5),
              new PlaceFlatModel(idplace, 18, 0, 5),
              new PlaceFlatModel(idplace, 19, 0, 5),
              new PlaceFlatModel(idplace, 20, 0, 5)]
      ),

      new PlaceFloorModel(6,
      [new PlaceFlatModel(idplace, 21, 0, 6),
              new PlaceFlatModel(idplace, 22, 0, 6),
              new PlaceFlatModel(idplace, 23, 0, 6),
              new PlaceFlatModel(idplace, 24, 0, 6)]
      )
    ]);
  }

}



