import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import {PlaceModel} from "../../models/placemodel";
import {PlaceService} from "../../service/placeservice";
import {PlaceFormPage} from "../place-form/place-form";

@Component({
  selector: 'place-page',
  templateUrl: 'place.html'
})
export class PlacePage {

  places: PlaceModel[] = [];

  constructor(public navCtrl: NavController, private placeService: PlaceService) {

  }

  ionViewWillEnter() {
    this.refreshPlaceList();
  }

  create() {
    this.navCtrl.push(PlaceFormPage, {
      id: 0
    });
  }

  open(id) {
    this.navCtrl.push(PlaceFormPage, {
      id: id
    });
  }

  remove(id) {
    this.placeService.remove(id).then(() => {
      this.refreshPlaceList();
    });
  }

  refreshPlaceList() {
    this.placeService.getPlaces().then((placeList) => {
      this.places = placeList || [];
    })
  }
}



