
export class PlaceFlatModel {

  public placeid: number = 0;
  public num: number = 0;
  public type: FlatType = FlatType.DEFAULT; // 0 - default, 1 - good, 2 - visited, 3 - danger, 4 - new danger
  public floor: number = 0;

  public dangerComment: string;
  public noRing: boolean = false;
  public noDmf: boolean = false;

  constructor(placeid: number, num: number, type: FlatType, floor: number) {
    this.placeid = placeid;
    this.num = num;
    this.type = type;
    this.floor = floor;
  }

  public getId() {
    return this.placeid + "_" + this.num;
  }

}

export class PlaceFlatVisit {
  public time: number;
  public comment: string;
}

export enum FlatType {
  DEFAULT = 0,
  GOOD = 1,
  VISITED = 2,
  DANGER = 3,
  NEWDANGER = 4,
  SKIP = 5
}



