import {Component} from '@angular/core';
import {NavController, NavParams} from 'ionic-angular';
import {PlaceModel} from "../../models/placemodel";
import {PlaceService} from "../../service/placeservice";
import {PlaceFloorModel} from "../../models/placefloor";
import {FlatType, PlaceFlatModel} from "../../models/placeflat";

@Component({
  selector: 'place-form',
  templateUrl: 'place-form.html'
})
export class PlaceFormPage {

  public place: PlaceModel;
  public skip: number = 0;

  public input: PlaceFormInput = new PlaceFormInput();

  public floorPlan: PlaceFloorModel[] = [];

  public title: string = "Создать";

  isSaving: boolean = false;
  isUpdate: boolean = false;

  constructor(private navParams: NavParams, public navCtrl: NavController,
              private placeService: PlaceService) {
    const id = navParams.get("id");
    if (id > 0) {
      this.placeService.getPlace(id).then((place) => {
        console.log("pl", place);
        this.place = place;
        this.input.fillFromPlace(place);
        this.floorPlan = place.floorPlan;

        this.title = "№" + place.num + " " + place.title;
        this.isUpdate = true;

        this.skip = place.floorPlan[0].flatList.filter(value => value.type == FlatType.SKIP).length;
      })
    } else {
      this.floorPlan = this.genPlan();
    }
  }

  moveLeft() {
    this.skip--;
    this.refreshPlan();
  }

  moveRight() {
    this.skip++;
    this.refreshPlan();
  }

  refreshPlan() {
    this.floorPlan = this.genPlan();
  }

  genPlan() {
    let res: PlaceFloorModel[] = [];
    let cnt = 0;
    let dangerList: number[] = this.getDangerList();

    for (let floor:number = this.input.fromFloor - 1; floor < this.input.toFloor; floor++) {
      let floorModel = new PlaceFloorModel(floor + 1, []);
      for (let i:number = 0; i< this.input.perFloor; i++) {
        let flatNum = floor * +this.input.perFloor + +this.input.fromFlat + i - this.skip;
        if (cnt < this.skip) {
          floorModel.flatList.push(new PlaceFlatModel(0, 0, FlatType.SKIP, floor + 1));
        } else {
          floorModel.flatList.push(new PlaceFlatModel(0, flatNum,
            (dangerList.indexOf(flatNum) != -1 ? FlatType.DANGER : FlatType.DEFAULT), floor + 1));
        }
        cnt++;
      }
      res.push(floorModel);
    }

    return res;
  }

  getDangerList() {
    let list:number[] = [];
    if (this.floorPlan) {
      this.floorPlan.forEach((floor) => {
        if (floor.flatList) {
          floor.flatList.forEach((flat) => {
            if (flat.type == FlatType.DANGER) {
              list.push(flat.num);
            }
          })
        }
      })
    }
    return list;
  }

  save() {
    console.log("Save btn");
    this.isSaving = true;
    // this.input.dangetFloors = this.getDangerList().length;
    this.placeService.addPlace(this.input, this.floorPlan).then(() => {
      this.navCtrl.pop();
    });
  }
}

export class PlaceFormInput {
  public num: number =  null;
  public address: string = "";
  public perFloor:number = 4;
  public fromFloor:number = 1;
  public toFloor:number = 9;
  public fromFlat:number = 1;

  public dangetFloors: number = 0;

  fillFromPlace(model: PlaceModel) {
    this.num = model.num;
    this.address = model.title;
    this.perFloor = model.floorPlan[0].flatList.length;
    this.fromFloor = model.floorPlan[0].num;
    this.toFloor = model.floorPlan[model.floorPlan.length - 1].num;

    this.fromFlat = model.floorPlan[0].flatList.filter(value => value.type != FlatType.SKIP).shift().num;
  }

}



