import {Component, Input} from '@angular/core';
import {NavController} from 'ionic-angular';
import {FlatType, PlaceFlatModel} from "../../../models/placeflat";


@Component({
  selector: 'place-flatblock',
  templateUrl: 'placeflat-block.html'
})
export class PlaceFlatBlockComponent {
  @Input("flat") flat: PlaceFlatModel; // 0 - default, 1 - good, 2 - visited, 3 - danger, 4 - new danger, 5- skip/empty'

  flatType = FlatType;

  constructor(public navCtrl: NavController) {
  }

  toggle() {
    if (this.flat.type == FlatType.DANGER) {
      this.flat.type = FlatType.DEFAULT;
    } else {
      this.flat.type = FlatType.DANGER;
    }
  }

}



